@import "fonts";
@import "vars";
@import "global";

.header {
	max-height: 660px !important;
	margin-bottom: 45px;
	//height: 100vh;

	&__top {
		&-inner {
			padding: 10px 0;
			display: flex;
			align-items: center;
			justify-content: space-between;
			position: relative;
			z-index: 20;
		}
	}
	&__menu {
		background-color: #fff;
		&-list {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
		&-item {
			font-size: 16px;
			line-height: 20px;
			text-transform: uppercase;
			& + .header__menu-item {
				margin-left: 28px;
			}
		}
	}
	&__hero {
		height: 100% !important;
		background-image: url("../images/bg.jpg");
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
	}
	&__button {
		display: none;
		flex-direction: column;
		justify-content: space-between;
		width: 40px;
		height: 40px;
		border: none;
		outline: none;
		padding: 0;
		background-color: #fff;
		position: relative;
		cursor: pointer;
		span {
			display: block;
			width: 100%;
			height: 2px;
			background-color: #1e1e1e;
			transition: transform 0.3s;
		}
		&-active {
			span {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				&:nth-child(even) {
					display: none;
				}
				&:first-child {
					transform: rotate(45deg);
				}
				&:last-child {
					transform: rotate(-45deg);
				}
			}
		}
	}
}

.hero {
	&__inner {
		max-height: 592px;
		color: #ffffff;
		text-align: center;
		padding-bottom: 125px !important;
		padding-top: 90px;
		background: url("../images/hero_bg1.png") no-repeat left bottom -20px / 258px,
			url("../images/hero_bg2.png") no-repeat right bottom -50px / 258px;
		overflow: hidden;
	}
	&__suptitle {
		font-size: 34px;
		letter-spacing: 0px;
		line-height: 34px;

		font-weight: 300;
		margin-bottom: 15px;
	}
	&__title {
		font-size: 82px;
		line-height: 90px;
		font-weight: 700;
		padding-bottom: 28px;
		border-bottom: 4px solid #fff;
		margin: 0 auto 44px auto;
		max-width: 460px;
	}
	&__text {
		font-size: 24px;
		line-height: 26px;
		margin-bottom: 15px;
	}
	&__price {
		font-size: 36px;
		line-height: 36px;
		color: #ffffff;
		font-weight: 700;
		margin-bottom: 48px;
		span {
			font-size: 48px;
		}
	}
	&__button {
		display: inline-block;
		font-size: 16px;
		line-height: 16px !important;
		font-weight: 600;
		max-width: 260px;
		width: 100%;
		text-align: center;
		padding: 24px 0;
		text-transform: uppercase;
		border: 2px solid #ffffff;
		& + .hero__button {
			margin-left: 20px;
		}
	}
}

.section {
	text-align: center;
	&__title {
		font-size: 36px;
		line-height: 36px;
		font-weight: 700;
		padding-bottom: 25px;
		position: relative;
		text-transform: uppercase;
		margin-bottom: 25px;
		&::before {
			display: block;
			position: absolute;
			width: 90px;
			height: 4px;
			content: "";
			background-color: #1e1e1e;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
		}
	}
	&__text {
		margin-bottom: 25px;
	}
}

.slider {
	&__wrapper {
		height: 320px;
	}
	&__card {
		height: 100%;
		&-inner {
			height: 100%;
		}
		&-img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			overflow: hidden;
		}
	}
}

.swiper {
	width: auto;
	height: 320px;
}
.swiper-wrapper {
	transition-timing-function: linear;
	z-index: 100;
}

.sizes {
	background-image: url("../images/table_bg.png");
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	padding: 40px 0 50px 0;
}

.sizes {
	margin-bottom: 50px;
}

.table__sizes {
	width: 100%;
	font-size: 14px;
	line-height: 48px;
	border-collapse: collapse;

	&-wrapper {
		background-color: #fff;
		padding: 30px;
		box-shadow: (0 0 15px rgba(0, 0, 0, 0.15));
	}
	&-row {
		width: 100%;
		background-color: #fff;
		padding: 0 30px !important;
		td:first-child {
			padding-left: 30px;
		}
		td:last-child {
			margin-right: 30px;
		}
	}
	&-row:nth-child(odd) {
		background-color: #f3f3f3;
	}
	&-legend {
		font-weight: 700;
		text-align: left;
	}
	&-sizes {
		font-size: 700;
	}
}

.catalog {
	margin-bottom: 90px;
	&__tabs {
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 18px;
		text-align: center;
		text-transform: uppercase;
		margin-bottom: 30px !important;
	}
	&__handler {
		padding: 24px 35px;
		background-color: #f3f3f3;
		cursor: pointer;
		&-active {
			color: #fff;
			background-color: #ba0152;
		}
	}
	&__card {
		position: relative;
		//max-width: 300px;
		text-align: center;
		border: 1px solid #dbdbdf;
		font-size: 18px;
		overflow: hidden;
		&-img {
			max-height: 350px;
			height: 100%;
			width: 100%;
			object-fit: cover;
			object-position: center top;
			overflow: hidden;
			margin-bottom: 10px;
			cursor: pointer;
			transition: transform 0.3s;
			&:hover {
				transform: scale(1.3);
			}
		}
		&-info {
			padding: 0 20px 25px 20px;
		}
		&-title {
			font-weight: 700;
			margin-bottom: 10px;
		}
		&-sale {
			display: flex;
			width: 60px;
			height: 60px;
			text-align: center;
			align-items: center;
			justify-content: center;
			border-radius: 50%;
			background-color: #b4014e;
			font-size: 20px;

			color: #e6e6e6;
			font-weight: 700;
			position: absolute;
			top: 14px;
			left: 14px;
		}
		&-sizes {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 10px !important;
		}
		&-size {
			&.size-disable {
				color: #ceced3;
			}
		}
		&-size + .catalog__card-size {
			margin-left: 20px;
		}

		&-colors {
			display: flex;
			align-items: center;
			justify-content: center;
			margin-bottom: 10px !important;
		}
		&-color {
			display: block;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			&.color-black {
				background-color: #000000;
				border: 2px solid #dbdbdf;
			}
			&.color-red {
				background-color: #ff0000;
				border: 2px solid #dbdbdf;
			}
			& + .atalog__card-color {
				margin-left: 10px;
			}
		}
		&-price {
			font-size: 30px;
			color: #b4014e;
			font-weight: 700;
			margin-bottom: 15px;
			span {
				display: inline-block;
				margin-right: 20px;
				text-decoration: line-through;
				color: #8c8c8c;
				font-weight: 400;
			}
		}
		&-button {
			display: block;
			text-align: center;
			font-weight: 600;
			color: #fff;
			font-weight: 600;
			border-radius: 4px;
			border: none;
			outline: none;
			width: 100%;
			padding: 20px 0;
			text-transform: uppercase;
			cursor: pointer;
			box-shadow: (0px 4px 0px #375cb3);
			background-image: linear-gradient(0deg, #4775e5 0%, #8d53e9 100%);
		}
	}
}

.tabs {
	&__field {
		&-item {
			display: none;

			&.tabs__field-visible {
				display: block !important;
			}
		}
		&-list {
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			grid-template-rows: 1fr 1fr;
			grid-gap: 20px;
		}
	}
}

.order {
	background-color: #f7f2f4;
	padding: 75px 0 120px;
	&__howto {
		text-align: center;
		position: relative;

		&-list {
			display: flex;
			padding: 0px 20px !important;
			justify-content: space-between;
			align-items: fill;
		}
		&-item {
			max-width: 205px;
		}
		&-ico {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 auto;
			width: 130px;
			height: 130px;
			border-radius: 50%;
			background-color: #b4014e;
			border: 6px solid rgb(235, 76, 145);
			margin-bottom: 55px;
		}
		&-img {
			height: 50px;
			object-fit: cover;
			object-position: center;
			overflow: hidden;
		}
		&-description {
			font-size: 16px;
			line-height: 24px;
		}
	}
	&__underline {
		display: flex;
		justify-content: stretch;
		align-items: center;
		position: absolute;
		width: 75%;
		left: 50%;
		transform: translateX(-50%);

		align-self: center;
		bottom: 57px;
		&-line {
			position: relative;
			flex-basis: 33.3%;
			border-bottom: 2px dashed #ceced3;
			&::before {
				display: block;
				position: absolute;
				content: "";
				width: 16px;
				height: 16px;
				border-radius: 50%;
				background-color: #ceced3;
				top: -7px;
				left: -7px;
				border: 3px solid #dadadf;
			}
		}
		&:last-child {
			&:after {
				display: block;
				position: absolute;
				content: "";
				width: 16px;
				height: 16px;
				border-radius: 50%;
				background-color: #ceced3;
				top: -7px;
				right: -7px;
				border: 3px solid #dadadf;
			}
		}
	}
}

/* modal */
.modal {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	&.hidden {
		display: none;
	}
	&-wrapper {
		position: fixed;
		top: 50%;
		left: 50%;
		z-index:1000;
		max-width: 640px;
		width: 100%;
		max-height: 350px;
		overflow: hidden;
		transform: translate(-50%, -50%);

		&.catalog__card {
			display: flex;
			background-color: #fff;
			align-items: start;
			border: none;
			filter: drop-shadow(0 0 8px rgba(0, 0, 0, 0.3));
		}
		.catalog__card-info {
			flex: 0 1 50%;
			padding: 25px 15px;
		}
		.catalog__card-img {
			display: block;

			max-width: 328px;
			margin-bottom: 0;
			max-height: 100%;
			cursor: default;
			&:hover {
				transform: scale(1);
			}
		}
		.catalog__card-price,
		.catalog__card-title {
			margin-bottom: 9px;
		}
	}
	&__close {
		position: absolute;
		display: block;
		width: 15px;
		height: 15px;
		top: 10px;
		right: 10px;
		border: none;
		background-color: transparent;
		background-image: url("../images/close.png");
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
		cursor: pointer;
		&:focus {
			outline: none;
		}
	}
	.close {
		font-weight: bold;
		font-size: 16px;
		line-height: 1.5;
		color: white;
		border: none;
		background-color: transparent;
		&:focus {
			outline: none;
		}
	}
	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(0, 0, 0, 0.5);
		width: 100%;
		height: 100%;
		z-index:999;
	}

	&__input {
		padding: 11px 15px;
		border: 1px solid #ceced3;
		width: 100%;
		margin-bottom: 9px;
		text-align: center;
		line-height: 18px;
		font-size: 16px;
	}
}

@import "media";
